import { Component, OnInit, Input, ChangeDetectionStrategy, OnChanges } from '@angular/core';
import { Article } from '../article.model';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { PageUpdatedEventData } from 'src/app/theme/expandable-list/pageupdatedevent.model';
import { ArticlesService } from '../articles.service';
import { NotificationService } from 'src/app/theme/notification/notification.service';
import { MatDialog } from '@angular/material';
import { ConfirmationDialogueComponent } from 'src/app/theme/confirmation-dialogue/confirmation-dialogue.component';
import { ConfirmationDialogueInput, ConfirmationDialogueButtons } from 'src/app/theme/confirmation-dialogue/confirmation-dialogue-input';

@Component({
  selector: 'app-article-list',
  templateUrl: './article-list.component.html',
  styleUrls: ['./article-list.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0', display: 'none'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})

export class ArticleListComponent implements OnInit {
  articles:Article[] = new Array();
  columnsToDisplay = ['index','title','edit',];
  showDelete:boolean=true;
  columnsToDisplayInDetails = ['summary','title','body'];
  totalNumberOfArticles:number ;
  pageSize:number = 5;
  pageNumber:number=0;
  expandedRow;
  constructor(
    private articleService:ArticlesService,
    private notificationService:NotificationService,
    private dialog: MatDialog) {}

  ngOnInit() {
    this.articleService.articleCreatedEvent.subscribe(event=>{
      let pn = this.pageNumber;
      if(this.articles.length==this.pageSize){
        pn=this.pageNumber+1;
        console.log(this.articles.length);

      }
      this.onPageChange({length: 0,
        pageIndex: pn,
        pageSize: this.pageSize,
        previousPageIndex: this.pageNumber});
    }) ;   
    this.onPageChange({length: 0,
      pageIndex: this.pageNumber,
      pageSize: this.pageSize,
      previousPageIndex: 0});
  }
  ngOnChanges(){
    this.ngOnInit();
  }
  onPageChange(event:PageUpdatedEventData){
    this.articleService.getArticles(event.pageSize,event.pageIndex)
    .subscribe(response => {
      if(response.status.status==="SUCCESS"){
          this.articles = response.dataList;
          this.totalNumberOfArticles=response.totalsize;
          this.pageNumber = event.pageIndex;
          this.pageSize = event.pageSize;
      }
      else{
          this.notificationService.sendNotification("Failure");
      }
  },
  error =>{
    console.log(error);
    console.log("an error has  been caught")
    this.notificationService.sendNotification("Kuch to hua hai");
  });
  }

  onDeleteClicked(){
    console.log("Article Deletion request received.")
    console.log(this.expandedRow);
    let dialogInput:ConfirmationDialogueInput = new ConfirmationDialogueInput();

    const dialogRef = this.dialog.open(ConfirmationDialogueComponent, {
      width: '50%',
      data: {
        message: "Are you sure, you want to delete this article?",
        buttons:[
          new ConfirmationDialogueButtons("Yes","deletion-approved",'primary'),
        ],
        closeButtons:[
          "No"
        ]

      }
    });


    dialogRef.afterClosed().subscribe(result => {
      if(result=="deletion-approved"){
        this.onDeleteConfirmation(this.expandedRow.id);
      }
    });
  }
  onDeleteConfirmation(id:number){
   this.articleService.deleteArticle(id).subscribe(response=>{
     if(response.status=="SUCCESS"){
        this.notificationService.sendNotification("Article deleted successfully!");
        if(this.articles.length == 1 &&this.pageNumber!=0){
          this.pageNumber = this.pageNumber-1;
        }
        this.ngOnInit();
     }
     else{
      this.notificationService.sendNotification("Failure deleting the article!");
     }
   },
   error =>{
    console.log(error);
    console.log("an error has  been caught")
    this.notificationService.sendNotification("Kuch to hua hai");
  });
  }
 
  toggleExpandedRow(row){
    if(this.expandedRow != row){
      this.expandedRow = row;
    }else{
      this.expandedRow = '';
    }
  }
}
