import { Component, OnInit } from '@angular/core';
import { UserService } from '../user.service';
import { User } from '../user.model';
import {animate, state, style, transition, trigger} from '@angular/animations';
import { MatSnackBar } from '@angular/material';
import { NotificationComponent } from 'src/app/theme/notification/notification.component';
import { NotificationService } from 'src/app/theme/notification/notification.service';

@Component({
  selector: 'app-users-list',
  templateUrl: './users-list.component.html',
  styleUrls: ['./users-list.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0', display: 'none'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class UsersListComponent implements OnInit {
  private users : User[];
  private columnsToDisplay: string[] = ['id','username','firstName','lastName'];
  private columnsToDisplayInDetails: string[] = ['id','username','firstName','lastName',
  'password','accountNonExpired','accountNonLocked','credentialsNonExpired','enabled'];
  private expandedUser: User;
  private totalNumberOfUsers:number;
  private pageSize:number = 5;
  private pageNumber : number= 0;
  constructor(private userService:UserService,
    private notificationService:NotificationService) { }

  ngOnInit() {
    this.onPageChange({length: 0,
      pageIndex: this.pageNumber,
      pageSize: this.pageSize,
      previousPageIndex: 0});
  }

  onPageChange(event){

    this.userService.getUsers(event.pageSize,event.pageIndex)
    .subscribe(response => {
      if(response.status.status==="SUCCESS"){
          this.users = response.dataList;
          this.totalNumberOfUsers=response.totalsize;
      }
      else{
        //Throw error that users cannot be found
          this.notificationService.sendNotification("Failure");
        
      }
  },
  error =>{
    console.log(error);
    console.log("an error has  been caught")
    this.notificationService.sendNotification("Kuch to hua hai");
  });
  }

} 

