import { Component, OnInit, Input, Output ,EventEmitter} from '@angular/core';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { PageUpdatedEventData } from './pageupdatedevent.model';
import { ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-expandable-list',
  templateUrl: './expandable-list.component.html',
  styleUrls: ['./expandable-list.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0', display: 'none'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class ExpandableListComponent implements OnInit {

  constructor(private route:ActivatedRoute) { }
  @Input('listData') listDataSource ;
  @Input('listColumns') columnsToDisplay ;
  @Input('detailsColumns') columnsToDisplayInDetails? ;
  @Input('totalNumberOfItems')totalNumberOfItems;
  @Input('pageSize')pageSize:number=5;
  @Input('pageNumber')pageNumber:number=1;
  @Output() pageChanged:EventEmitter<PageUpdatedEventData> = new EventEmitter<PageUpdatedEventData>();
  @Output() onDelete:EventEmitter<PageUpdatedEventData> = new EventEmitter<PageUpdatedEventData>();

  expandedRow;
  ngOnInit() {
    this.route.params.subscribe((params:Params) => {
      this.getPaginatedItems({length:0,pageIndex:1,pageSize:5,previousPageIndex:1});
   });
  }
  toggleExpandedRow(row){
    if(this.expandedRow != row){
      this.expandedRow = row;
    }else{
      this.expandedRow = '';
    }
  }
  getPaginatedItems(event){
    let paginationUpdate:PageUpdatedEventData = new PageUpdatedEventData();
    paginationUpdate.length=event.length;
    paginationUpdate.pageIndex=event.pageIndex;
    paginationUpdate.pageSize=event.pageSize;
    paginationUpdate.previousPageIndex=event.previousPageIndex;
    this.pageChanged.emit(paginationUpdate);
  }
}
