import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Article } from '../article.model';
import { ArticlesService } from '../articles.service';
import { NotificationService } from 'src/app/theme/notification/notification.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-create-article',
  templateUrl: './create-article.component.html',
  styleUrls: ['./create-article.component.css']
})
export class CreateArticleComponent implements OnInit {

  constructor(private articleService:ArticlesService,
    private notificationService:NotificationService,
    private router:Router) { }

  ngOnInit() {
  }
  createArticle(form:NgForm){
    
    if(form.valid){
      let article:Article = new Article();
      article.body=form.value.body;
      article.summary=form.value.summary;
      article.title = form.value.title;
      this.articleService.createNewArticle(article).subscribe(response=>{
        if(response.status.status=="SUCCESS"){
            this.notificationService.sendNotification("Article created successfully!");
            this.articleService.articleCreatedEvent.emit(response.data);
            form.resetForm();
        }else{
          this.notificationService.sendNotification("Article creattion Failed!");
        }
      });
    }
  }
}
