import { Component, OnInit } from '@angular/core';
import { Time } from '@angular/common';
import { ArticlesService } from '../articles.service';

@Component({
  selector: 'app-articles',
  templateUrl: './articles.component.html',
  styleUrls: ['./articles.component.css']
})
export class ArticlesComponent implements OnInit {

  private showCreateArticleComponent:boolean=false;
  constructor(private articleService:ArticlesService) { }

  ngOnInit() {
    this.articleService.articleCreatedEvent.subscribe(event=>{
      this.onArticleCreation();
    }) ; 
  }

  onAddArticleClick(){
    this.showCreateArticleComponent = !this.showCreateArticleComponent;
  }
  onArticleCreation(){
    this.showCreateArticleComponent = false;
  }
}
