import { environment } from "./environment";

export class BackendAPIService {
    getDeleteArticleAPI(id: number): string {
      return environment.backendUrl + environment.contextName + 'article'+'/'+id;
    }
    getCreateArticleAPI(): string {
      return environment.backendUrl + environment.contextName + 'article';
  }
  public getArticleListAPI(pageSize: number, pageNumber: number): string {
    return environment.backendUrl + environment.contextName + 'article/list?pageSize=' + pageSize + "&pageNumber=" + pageNumber + "&sortBy=id";
  }
  public getUsersListAPI(pageSize: number, pageNumber: number) {
    return environment.backendUrl + environment.contextName + 'user/list?pageSize=' + pageSize + "&pageNumber=" + pageNumber + "&sortBy=id";
  }
}