import { Injectable, EventEmitter } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { BackendAPIService } from "src/environments/backendAPIs";
import { PaginatedListResponse } from "../responseTypes/paginated-list-response.model";
import { Observable } from "rxjs";
import { Article } from "./article.model";
import { DataResponse } from "../responseTypes/data-response.model";
import { StatusResponse } from "../responseTypes/status-response.model";

@Injectable()
export class ArticlesService{
    constructor(private http: HttpClient,
        private backendAPIs:BackendAPIService) {}
        public articleCreatedEvent:EventEmitter<Article> = new EventEmitter();

        getArticles(pageSize:number,pageNumber:number) :Observable<PaginatedListResponse>{
            return this.http.get<PaginatedListResponse>(this.backendAPIs.getArticleListAPI(pageSize,pageNumber));
         }  
        createNewArticle(article:Article) :Observable<DataResponse>{
            return this.http.post<DataResponse>(this.backendAPIs.getCreateArticleAPI(),article);
        }
        deleteArticle(id:number)  :Observable<StatusResponse>{
            return this.http.delete<StatusResponse>(this.backendAPIs.getDeleteArticleAPI(id));
        }
}